.search-input {
  font-size: 16px;
  width: 100%;
  position: relative;
  top: -6px;
  outline: none;
  border: none;
}

.search-result-container {
  height: calc(100vh - 210px);
  overflow-y: hidden;
}

.search-result-container--title {
  margin-bottom: 12px;
}

.search-result-container--products {
  height: calc(100vh - 262px);
  overflow-y: scroll;
}

.search-result-container--products--b {
  height: calc(100vh - 262px);
  overflow-y: scroll;
  -webkit-box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.35);
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.35);
}

.search-result-product--container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.search-result-product--link {
  border-top: 1px solid lightgray;
  margin-top: 8px;
  padding-top: 8px;
  text-align: right;
  font-size: 12px;
  color: #42B549;
}

.search-result-product--link:hover, .search-result-product--link:active {
  -webkit-filter: brightness(120%);
          filter: brightness(120%);
}

.search-result-product--image {
  display: block;
  width: 120px;
  height: 120px;
  border-radius: 10px;
  -o-object-fit: contain;
     object-fit: contain;
}
.search-result-product--info {
  display: block;
  margin-left: 8px;
  -ms-flex: 1 1;
      flex: 1 1;
  width: calc(100% - 150px);
}
.search-result-product--info--price {
  display: block;
  color: #ff5722;
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  width: 100%;
}
.search-result-product--info--shop {
  display: block;
  color: rgba(0, 0, 0, .72);
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  width: 100%;
}
.search-result-product--empty {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  color: #777;
  font-weight: 200;
}


